<template>
  <PopupAddWrapper
    :hidden="hidden"
    header="–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –≥–æ—Ä–æ–¥–∞"
    @popup-close="$emit('popup-toggle')"
    @popup-add="addCity(addedCity)"
  >
    <template #popup-add-content>
      <AppLineText
        inputID="input-city"
        label="–ù–∞–∑–≤–∞–Ω–∏–µ: "
        maxLength="20"
        v-model="addedCity.name"
      />
    </template>
  </PopupAddWrapper>
</template>

<script>
import PopupAddWrapper from '../../popups/PopupAddWrapper'
import AppLineText from '../../AppLineText'

export default {
  name: 'CityPopupAdd',

  components: {
    PopupAddWrapper,
    AppLineText
  },

  props: { hidden: Boolean },

  data () {
    return {
      addedCity: {},
      firstNote: '–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'
    }
  },

  created () {
    this.renewAddedData()
  },

  watch: {
    firstNote: function (note) {
      this.addedCity.notes[0] = note
    }
  },

  methods: {
    renewAddedData () {
      this.addedCity = {
        id: Date.now(),
        name: '',
        notes: ['–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'],
        notesCount: '5'
      }

      this.firstNote = '–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'
    },

    addCity (city) {
      this.$emit('add-city', city)
      this.renewAddedData()
    }
  }
}
</script>
